@import '../../_vars.sass'
    

.controls
    display: flex
    flex-direction: row
    height: 110px
    background: #3a3a3a
    color: $white
    align-items: center
    width: 100vw
    position: fixed
    bottom: 0
    left: 0
    transition: .8s ease all
    z-index: 999
    transform: translateY(100%)
    &.active
        transform: translateY(0)
        
.music__icon
    cursor: pointer

.music__controls
    display: flex
    align-items: center
    margin: 0 20px 0 100px

.hide 
    display: none

.random, .repeat
    cursor: pointer
    &.active
        svg
            fill: #1085bb

input[type=range] 
    -webkit-appearance: none
    margin: 10px 0
    width: 100%
    &:focus 
        outline: none

    &::-webkit-slider-runnable-track 
        width: 100%
        height: 5px
        cursor: pointer
        animate: 0.2s
        box-shadow: 0px 0px 0px #000000
        background: #4aa9de
        border-radius: 5px
        border: 0px solid #000000

    &::-webkit-slider-thumb 
        box-shadow: 0px 0px 1px #000000
        border: 0px solid #000000
        height: 13px
        width: 4px
        border-radius: 50px
        background: #2a6586
        cursor: pointer
        -webkit-appearance: none
        margin-top: -4px

    &:focus::-webkit-slider-runnable-track 
        background: #4aa9de

    &::-moz-range-track 
        width: 100%
        height: 5px
        cursor: pointer
        animate: 0.2s
        box-shadow: 0px 0px 0px #000000
        background: #4aa9de
        border-radius: 5px
        border: 0px solid #000000

    &::-moz-range-thumb 
        box-shadow: 0px 0px 1px #000000
        border: 0px solid #000000
        height: 13px
        width: 4px
        border-radius: 50px
        background: #2a6586
        cursor: pointer

    &::-ms-track 
        width: 100%
        height: 5px
        cursor: pointer
        animate: 0.2s
        background: transparent
        border-color: transparent
        color: transparent

    &::-ms-fill-lower 
        background: #4aa9de
        border: 0px solid #000000
        border-radius: 10px
        box-shadow: 0px 0px 0px #000000

    &::-ms-fill-upper 
        background: #4aa9de
        border: 0px solid #000000
        border-radius: 10px
        box-shadow: 0px 0px 0px #000000

    &::-ms-thumb 
        box-shadow: 0px 0px 1px #000000
        border: 0px solid #000000
        height: 13px
        width: 4px
        border-radius: 50px
        background: #2a6586
        cursor: pointer
    &:focus::-ms-fill-lower 
        background: #4aa9de
    &:focus::-ms-fill-upper 
        background: #4aa9de

.plsoptions 
    justify-self: flex-end

.volume
    cursor: pointer

.progressb
    width: 100%

.vlme 
    display: flex
    align-content: center
    overflow: hidden
    width: 25px
    margin-left: 10px
    transition: all 500ms
    position: absolute
    left: 0
    &:hover
        width: 90px

.volume
    padding: 5px

#volBar 
    padding: 0
    margin: 0
    width: 50px
    background: transparent
    &::-moz-range-thumb 
        height: 10px
        width: 3px
        background: #2A6586
        border-radius: 5px
        cursor: pointer

.controls__close
    position: absolute
    top: 10px
    right: 80px
    cursor: pointer
    background: none
    color: $white
    font-size: 18px
    border: none
    font-weight: 800
    
.plsoptions
    margin: 0 20px

@media screen and (max-width: 768px)
    .music__controls
        margin: 0 20px

    .vlme
        bottom: 0


    .play
        .music__icon
            height: 40px
            width: 40px

    .music__icon
        height: 30px
        width: 30px

    .plsoptions
        margin: 0 4px 0 15px

    .controls__close
        top: 0
        right: 0