.container_J7uAK
    display: none
    
.menu__musics
    display: flex
    flex-direction: column
    width: 100%
    height: 100%

.invisib-div
    margin-top: 60px
    width: 100%
    min-height: 90px
    transition: .8s
    &.hide-div
        margin: 0
        min-height: 40px

.menu__musics-content
    position: relative
    z-index: 999
    padding: 20px
    width: 60%
    height: 100%
    background-color: #2d2d2d
    box-shadow: 0 0 10px 0 black
    display: block
    overflow: auto

.menu__audio
    width: 80%
    height: 12%
    margin-bottom: 20px

.audio
    width: 80%
    