.cart-block
    display: flex
    align-items: center
    position: relative

.cart-block__total-price
    margin-left: 15px
    font-size: 18px
    font-weight: bold

.cart-block__icon, .cart-block__icon__adaptive
    cursor: pointer
    position: relative
    display: block

.cart-block__icon__adaptive
    display: none

.user
    margin: 0 30px
    
.music, .user
    cursor: pointer

@media screen and (max-width: 768px)
    .cart-block__icon
        display: none

    .cart-block__icon__adaptive
        display: inline-block