@import '../../../_vars.sass'
    
.clothes-item
    text-decoration: none
    color: $white
    width: 320px
    height: 500px
    position: relative
    background: rgb(36, 37, 41)
    border-radius: 3px
    border: 1px solid #737373
    cursor: pointer
    transition: .2s
    opacity: .95
    &:hover
        opacity: 1
        transform: scale(1.05)

.clothes-item__title
    font-size: 18px
    font-weight: bold

.clothes-item__gender-sale
    margin-top: 10px
    display: flex
    align-items: center
    justify-content: space-between
    flex-wrap: wrap

.clothes-item__details
    padding: 10px 20px

.clothes-item__description
    margin-top: 10px
    font-size: 14px

.clothes-item__buy

    position: absolute
    right: 20px
    bottom: 10px
    margin-top: 15px

@media screen and (max-width: 768px)
    .clothes-item
        width: 280px