.profile-page
    padding: 50px 0

.profile-page__title
    margin: 0 auto
    font-size: 40px
    width: max-content
    margin-bottom: 20px

.profile__links
    display: flex
    justify-content: space-around
    width: 100%
    flex-wrap: wrap
    border-bottom: 2px solid #d1d1d1
    padding-bottom: 30px

.profile__link
    border-bottom: 2px dashed #d5d5d5
    padding-bottom: 2px
    margin: 15px 20px 0 15px
    transition: .2s
    &:hover
        transform: scale(1.1)
        color: lime
        border-bottom: 2px dashed lime
        &:last-child
            color: red
            border-bottom: 2px dashed red            
