.profile-contacts-page, .ordering-address-page
    display: flex
    justify-content: center
    align-items: center
    flex-direction: column
    
.profile-contacts-page__title, .ordering-address-page__title
    font-size: 30px
    
.profile-contacts-form, .ordering-address-form
    width: 90%
    display: flex
    align-items: center
    flex-direction: column
    font-size: 18px
    div
        width: 100%
        display: flex
        flex-direction: column
    label
        width: 100%

.profile-input
    width: 100%
    font-family: 'Oswald', sans-serif
    padding: 10px 15px
    border-radius: 5px
    background: #fff
    border: 1px solid #343434
    opacity: .9
    &:focus
        opacity: 1

    
.profile-contacts-save, .ordering-address-save, .profile-input
    margin-top: 20px