.clothes-page__content 
    display: flex

.clothes-page__left 
    width: 50%
    min-height: 600px
    margin-right: 30px
    .clothes-cover
        min-height: 600px

    .test
        height: 50px

    .new-item
        font-size: 44px
        line-height: 44px

.clothes-page__right 
    width: 50%
    height: 100%
    position: relative

    .clothes-item__buy
        bottom: 0

    .clothes__sale-buy
        margin-top: 10px 
        display: flex
        justify-content: space-between
        align-items: center

.clothes-page__description
    margin: 10px 0

@media screen and (max-width: 768px)
    .clothes-page__content
        flex-direction: column

    .clothes-page__right
        width: 100%
        padding-bottom: 30px
        .clothes-item__buy
            left: 0

    .clothes-page__left
        width: 100%
        min-height: 450px
        .clothes-cover
            min-height: 450px