.burger
    display: none
    
.burger-menu
    height: 30px
    width: 30px
    z-index: 50
    position: absolute
    display: flex
    align-items: center
    left: 70px
    top: 40px

.burger-btn
    width: 30px
    height: 20px
    position: relative
    cursor: pointer
    &::before
        content: ''
        position: absolute
        top: 0
        width: 30px
        background-color: white
        height: 2px
    &::after
        content: ''
        position: absolute
        bottom: 1px
        width: 30px
        background-color: white
        height: 2.5px
    
    span
        position: absolute
        top: 8px
        width: 30px
        background-color: white
        height: 2px   

@media screen and (max-width: 1440px)
    .burger
        display: block
    
    .burger-menu
        left: 30px