.header__categories-title
    margin-left: 62px
    width: max-content
    text-transform: uppercase
    letter-spacing: 0.04em
    transition: .3s
    &:hover
        transform: scale(1.1)
        border-bottom: 2px dashed #fff
        padding-bottom: 2px
    &:nth-child(5)
        margin-right: 62px

.header__svg-search
    margin-right: 42px
    margin-left: 10px


@media screen and (max-width: 1440px) 
    .header__categories-title
        margin-left: 5px
        margin-bottom: 25px

    .header__input-search
        width: 80%

    .header__svg-search
        display: none

@media screen and (max-width: 768px)
    .header
        padding: 0 30px