@import '../../../_vars.sass'
    
.clothes-cover, .order-cover
    height: 65%
    width: 100%
    background-size: cover
    background-position: center
    border-top-left-radius: 3px
    border-top-right-radius: 3px
    position: relative

.triangle-sale
    position: absolute
    font-family: 'Mouse Memoirs', sans-serif
    font-size: 24px
    top: 6px
    right: 3px
    color: #03005b

.triangle-right
    width: 0
    height: 0
    position: absolute
    top: 0
    right: 0
    color: $black
    border-top: 65px solid #b40000
    border-left: 65px solid transparent

.test
    width: 100%
    position: absolute
    bottom: 0
    right: 0
    left: 0
    height: 30px
    background: #5d00d7

.new-item
    font-family: 'Poppins', sans-serif
    text-transform: uppercase
    position: absolute
    bottom: 2px
    font-size: 25px
    letter-spacing: 1.5px
    line-height: 25px
    right: 0
    left: 0
    text-align: center