@import url('https://fonts.googleapis.com/css2?family=Mouse+Memoirs&family=Oswald:wght@300;400;500;600;700&family=Poppins:wght@300;400;500;600;700&display=swap')
@import './_vars.sass'

body, html
    height: 100%
    width: 100%

#root
    height: 100%

body 
    margin: 0
    padding: 0
    font-family: -apple-system, BlinkMacSystemFont, 'Oswald', sans-serif
    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale
    overflow-x: hidden
    -webkit-touch-callout: none
    -webkit-user-select: none
    -khtml-user-select: none
    -moz-user-select: none
    -ms-user-select: none
    user-select: none

code 
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace

    
.App
    max-width: 100%
    color: $white
    height: 100%

.main-wrapper
    display: flex

    height: 100%
    flex-direction: column
    position: relative
    
        flex: 1 1 auto

a
    text-decoration: none
    color: $white

p
    margin: 0

li
    list-style: none

h1, h2
    margin: 10px 0


.animate-wrapper
    position: fixed
    width: 100%
    height: 100%
    z-index: 104
    background: $bg-color
    box-shadow: 0px 20px 30px 2px #000000
    background-repeat: no-repeat
    background-position: center center
    &:first-child
        background-image: url('./assets/made.png')
    &:nth-child(2)
        z-index: 103
        background: #303030
        background-image: url('./assets/by.png')
        background-repeat: no-repeat
        background-position: center center
    &:nth-child(3)
        background: #262626
        z-index: 102
        box-shadow: none
        background-image: url('./assets/flex.png')
        background-repeat: no-repeat
        background-position: right center
    &:nth-child(4)
        background: #1e1e1e
        z-index: 102
        box-shadow: none
        background-image: url('./assets/team.png')
        background-repeat: no-repeat
        background-position: left center
    
.upward
    position: fixed
    bottom: 50px
    right: 50px
    cursor: pointer
    opacity: 0
    transition: .2s
    width: 50px
    height: 50px
    display: flex
    justify-content: center
    align-items: center
    background: #00000083
    border: 5px solid $bg-color
    border-radius: 50%
    &.active
        opacity: 1
        

.audio
    display: block !important
    position: absolute
    z-index: 0


.secondary-text
    color: rgb(80, 82, 84)
    
.page, h1, .loader-page
    flex: 1 1 auto

.loader-page
    display: flex
    justify-content: center

.page, .input__empty
    padding: 50px 35px
    
.category-page
    display: flex
    flex-wrap: wrap
    justify-content: center
    align-content: flex-start
    gap: 40px

.header-footer__styles
    background: #313131
    transition: .3s
    min-height: 110px

    &:hover

        background: #242424

@media screen and (max-width: 768px)
    .page
        padding: 50px 20px
