.footer__inner
    height: 100%
    display: flex
    justify-content: space-evenly
    align-items: center
    position: relative
    span
        position: absolute
        bottom: 5px
        right: 20px
        font-size: 20px
        text-transform: uppercase
        color: #ffffff86
        font-family: 'Poppins', sans-serif
        letter-spacing: .07em
        font-weight: 600

.footer
    box-shadow: 0 0 20px 0 black