@import '../../_vars.sass'

.form-auth
    background: $bg-color
    color: $white
    max-width: 70%
    padding: 20px
    margin: 0 auto
    border-radius: 15px
    h4
        margin: 0

    .btn
        transition: .3s
        
        &:hover
            background: #48d72b

.not-registr, .has-account, .reset-password
    margin: 0 20px 0 0

.link-for-registr, .link-for-login, .link-for-reset
    padding-bottom: 1px
    border-bottom: 2px dashed #3f94fb
    color: #0973f4

.login-links
    margin-top: 10px
    
label
    display: flex
    flex-direction: column

.input
    width: 92%
    padding: 5px 10px
    border-radius: 10px
    margin: 5px 0 0 0
    font-family: 'Oswald', sans-serif

.btn-input-auth, .btn-input-reset
    margin-top: 10px

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button
    -webkit-appearance: none
    margin: 0

