.btn
    border-radius: 15px
    color: white
    border: none
    padding: 0 10px
    cursor: pointer
    font-family: 'Oswald', sans-serif

.btn--primary 
    background-color: rgb(85, 88, 237)

.btn--secondary 
    background-color: rgb(80, 82, 84)

.btn--not
    background-color: #474747
    cursor: auto

.btn--padding
    padding: 5px 10px

.btn--small 
    font-size: 16px

.btn--medium 
    font-size: 18px
