@import '../../_vars.sass'

.header
    display: flex
    align-items: center
    justify-content: space-between
    padding: 0 70px
    max-width: 100%
    box-shadow: 0 0 20px 0 black

.header__input-search
    background: rgba(0, 0, 0, 0.3)
    border: 1px solid #8B8B8B
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25)
    border-radius: 15px
    padding: 7px 13px
    margin-left: 5px
    color: $white
    width: 250px
    font-family: 'Oswald', sans-serif
    text-transform: uppercase

.flex
    display: flex
    flex-grow: 1
    align-items: center
    justify-content: space-between

.header__cart-btn-wrapper
    justify-content: flex-end

.header__store-title
    color: $white
    text-decoration: none
    position: relative
    width: max-content
    font-size: 24px
    font-weight: bold

.logo__not-auth
    width: 110px
    height: 100px
    margin: 0 auto

@media screen and (max-width: 1440px)
    .header__desktop
        display: none

    .header__store-title
        margin-bottom: 30px

    .header__cart-btn-wrapper
        justify-content: flex-end