@import '../../_vars.sass'

.sort 
    position: relative
    width: 90%
    margin: 0 auto
    &__label 
        display: flex
        align-items: center
        justify-content: flex-end
        span
            margin-right: 25px
            font-weight: 800
            cursor: pointer
            &.active
                border-bottom: 2px dashed orange
                color: orange
                
        strong 
            margin-right: 8px
            color: $white
            border-bottom: 2px dashed $white
            cursor: pointer


    &__popup 
        display: none
        &__active 
            display: block
            position: absolute
            z-index: 88
            right: 0
            margin-top: 15px
            background: $bg-color
            box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.09)
            border-radius: 10px
            overflow: hidden
            padding: 10px 0
            width: 160px
            ul 
                overflow: hidden
                padding: 0
            li 
                padding: 12px 20px
                cursor: pointer
                &.active
                &:hover 
                    background: rgba(254, 95, 30, 0.05)
            
                &.active 
                    font-weight: bold
                    color: orange
            
        
      
    
  
