@import '../../../_vars.sass'

.size-btn
    background: #000000c5
    border: 3px solid #4a4a4a
    padding: 10px 15px
    border-radius: 3px
    color: $white
    font-weight: 800
    font-family: 'Oswald', sans-serif
    cursor: pointer
    margin-right: 7px
    margin-top: 10px
    transition: .3s
    &.active
        background: $white
        border: 3px solid #292929
        color: $black
    &:hover
        transform: scale(1.1)
    &:active
        box-shadow: 0px 0px 30px $white
        transform: scale(1.05)